    height: 24px;

    background-position: -5px -5px;
}

.sprite-inst {
    width: 24px;
    height: 24px;

    background-position: -39px -5px;
}

.sprite-search {
    width: 24px;
    height: 24px;

    background-position: -73px -5px;
}

.sprite-tube {
    width: 24px;
    height: 24px;

    background-position: -107px -5px;
}

.sprite-twit {
    width: 24px;
    height: 24px;

    background-position: -141px -5px;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0; 

    outline: none;
}

html {
    font-size: 62.5%;
}

img {
    width: 100%;
    max-width: 100%;
}

a:hover {
    text-decoration: none;
}

header {
    padding-top: 50px;
}

.bannerLine {
    padding-top: 30px;
    padding-bottom: 30px;
}
.bannerLine_block-href {
    display: flex;
    flex-direction: column;

    font-weight: bold; 

    text-transform: uppercase;

    align-items: flex-end;
}
.bannerLine_block-href .shop {
    font-family: 'Heebo', sans-serif;
    font-size: 2.1rem;

    color: #b6b8ba;
}
.bannerLine_block-href .social {
    display: flex;

    padding: 18px 0;
}
.bannerLine_block-href .donate {
    font-family: 'Heebo', sans-serif;
    font-size: 2.1rem;

    color: #f36f21;
}

.navigationHeader {
    position: absolute;
    z-index: 12;
    top: 0;
    bottom: 0;
    left: -100%;

    display: flex;
    flex-direction: column;

    width: 80%;
    padding: 20px;
    padding-bottom: 25px;

    background-color: #272c31;

    transition: .2s; 

    align-items: flex-start;
}
.navigationHeader_href {
    margin: 0 8px; 

    font-family: 'Heebo', sans-serif;
    font-size: 2.1rem;
    font-weight: 600;

    color: #6d6e71;

    text-transform: uppercase;
}
.navigationHeader_href:hover {
    color: #f36f21;
}
.navigationHeader_href:active {
    color: #f36f21;
}

.showMenu {
    left: 0;
}

.hamburger-inner,
.hamburger-inner::after,
.hamburger-inner::before {
    background-color: black;
}

.hamburger {
    display: inline-block;
    overflow: visible; 

    margin: 0;

    font: inherit;

    color: inherit;
    border: 0;
    background-color: transparent;

    cursor: pointer;
    transition-timing-function: linear;
    transition-duration: .15s;
    transition-property: opacity, -webkit-filter;
    transition-property: opacity, filter;
    transition-property: opacity, filter, -webkit-filter;
    text-transform: none;
}

.hamburger:hover {
    opacity: .7;
}

.hamburger-box {
    position: absolute;
    top: 15px;
    right: 15px; 

    display: inline-block;

    width: 40px;
    height: 24px;
}

.hamburger-inner {
    top: 50%;

    display: block;

    margin-top: -2px;
}

.hamburger-inner,
.hamburger-inner::after,
.hamburger-inner::before {
    position: absolute;

    width: 40px;
    height: 4px;

    border-radius: 4px;
    background-color: black;

    transition-timing-function: ease; 
    transition-duration: .15s;
    transition-property: -webkit-transform;
    transition-property:         transform;
    transition-property:         transform, -webkit-transform;
}

.hamburger-inner::after,
.hamburger-inner::before {
    display: block; 

    content: '';
}

.hamburger-inner::before {
    top: -10px;
}

.hamburger-inner::after {
    bottom: -10px;
}

.hamburger--slider .hamburger-inner {
    top: 2px;
}

.hamburger--slider .hamburger-inner::before {
    top: 10px;

    transition-timing-function: ease;
    transition-duration: .15s; 
    transition-property: opacity, -webkit-transform;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
}

.hamburger--slider .hamburger-inner::after {
    top: 20px;
}

.hamburger--slider.is-active .hamburger-inner {
    -webkit-transform: translate3d(0, 10px, 0) rotate(45deg);
            transform: translate3d(0, 10px, 0) rotate(45deg);
}

.hamburger--slider.is-active .hamburger-inner::before {
    opacity: 0; 

    -webkit-transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
            transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
}

.hamburger--slider.is-active .hamburger-inner::after {
    -webkit-transform: translate3d(0, -20px, 0) rotate(-90deg);
            transform: translate3d(0, -20px, 0) rotate(-90deg);
}

@media (min-width: 768px) {
    header {
        padding-top: 0;
    }
    .navigationHeader {
        position: relative;
        z-index: 2;
        top: auto;
        bottom: auto;
        left: auto;

        display: block;
        flex-direction: column;

        width: auto;
        padding: 0 0 25px 0;

        background-color: transparent;
    }
}

@media (min-width: 992px) {
    .bannerLine {
        padding-bottom: 50px;
    }
    .navigation_href {
        margin: 0 16px;
    }
}

.slider {
    padding-bottom: 60px;
}

.swiper-container {
    width: 100%;
    height: 18rem;
}

.swiper-slide {
    /* Center slide text vertically */
    display: flex;

    font-size: 18px;

    background: #fff;

    text-align: center;

    justify-content: flex-end;
    align-items: center;
}
.swiper-slide img {
    position: absolute;
}
.swiper-slide .text {
    z-index: 2;

    width: 66%;
    margin-right: 10%; 
    padding: 25px;

    font-family: 'Heebo', sans-serif;

    color: #fff;
    background-color: rgba(49, 47, 47, .811765);

    text-align: left;
}
.swiper-slide .text .title {
    padding-bottom: 1px;

    font-size: 1.9rem;
    line-height: 2rem; 

    text-transform: uppercase;
}
.swiper-slide .text .description {
    padding-bottom: 5px;

    font-size: 1.3rem;
    line-height: 1.4rem;
}
.swiper-slide .text .bookNow {
    padding: 1px 5px; 

    font-size: 1.8rem;
    line-height: 1.8rem;

    color: #fff;
    background-color: #f36f21;

    text-transform: uppercase;
}

.swiper-button-next,
.swiper-button-prev {
    top: auto;
    bottom: 2%;
}

.swiper-button-prev {
    background-image: url(../img/arrow-left.png);
}

.swiper-button-next {
    background-image: url(../img/arrow-right.png);
}

.swiper-pagination {
    left: calc(50% - 30px); 

    margin-top: 12px;
}

.swiper-pagination-bullet {
    margin: 0 6px;
}

.swiper-pagination-bullet-active {
    background: #000;
}

